	<div class="page-icons flex column" id="page-icons">
		<div class="page-header">
			<h1>Material Design Icons</h1>
			<h4 class="hidden-xs-only">in this page you can search <strong>Material Design Icons</strong> for your project</h4>
			<el-breadcrumb separator="/">
				<el-breadcrumb-item :to="{ path: '/' }"><i class="mdi mdi-home-outline"></i></el-breadcrumb-item>
				<el-breadcrumb-item>UI</el-breadcrumb-item>
				<el-breadcrumb-item>Icons</el-breadcrumb-item>
				<el-breadcrumb-item>Material Design Icons</el-breadcrumb-item>
			</el-breadcrumb>
		</div>

		<div class="search-wrap flex align-center">
			<div><i class="mdi mdi-magnify"></i></div>
			<input v-model="search" placeholder="Search an icon" class="box grow"/>
			<div class="icons-tot animated fadeInRight">{{iconsFiltered.length}} icons</div>
			<div class="animated fadeInRight"><a href="https://materialdesignicons.com/" target="_blank"><i class="mdi mdi-link-variant"></i>reference</a></div>
		</div>
		<resize-observer @notify="handleResize" />
		<div class="card-base card-shadow--medium box grow" id="icons-wrapper" v-loading="!asyncComponent">
			<component :is="asyncComponent"
				:cellSizeAndPositionGetter="cellSizeAndPositionGetter" 
				:collection="iconsFiltered" 
				:height="height" 
				:width="width" 
				class="icons-list scrollable only-y">

				<div slot="cell" slot-scope="props" class="icon">
					<i :class="'mdi mdi-'+props.data"></i>
					<div class="title">{{props.data}}</div>
					<div class="class">"mdi mdi-{{props.data}}"</div>
				</div>
			</component>
		</div>
	</div>
